"use client"

import { useState } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Badge } from '@/components/ui/badge'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import Link from 'next/link'

// Mock transaction data
const mockTransactions = [
  {
    id: 'TXN1703123456789',
    game: 'Mobile Legends: Bang Bang',
    package: '172 Diamond + 2 Bonus',
    amount: 30000,
    status: 'success',
    date: '2024-01-15 14:30:00',
    userId: '123456789',
    server: 'Server 1'
  },
  {
    id: 'TXN1703123456788',
    game: 'PUBG Mobile',
    package: '325 UC + 25 Bonus',
    amount: 79000,
    status: 'success',
    date: '2024-01-14 09:15:00',
    userId: '987654321',
    server: 'Asia'
  },
  {
    id: 'TXN1703123456787',
    game: 'Free Fire',
    package: '210 Diamond + 15 Bonus',
    amount: 40000,
    status: 'processing',
    date: '2024-01-13 16:45:00',
    userId: '456789123',
    server: 'Indonesia'
  },
  {
    id: 'TXN1703123456786',
    game: 'Genshin Impact',
    package: '330 Genesis Crystal + 30 Bonus',
    amount: 79000,
    status: 'success',
    date: '2024-01-12 11:20:00',
    userId: '789123456',
    server: 'Asia'
  },
  {
    id: 'TXN1703123456785',
    game: 'Mobile Legends: Bang Bang',
    package: '86 Diamond',
    amount: 15000,
    status: 'failed',
    date: '2024-01-11 20:10:00',
    userId: '123456789',
    server: 'Server 1'
  }
]

const statusColors = {
  success: 'bg-green-100 text-green-800',
  processing: 'bg-yellow-100 text-yellow-800',
  failed: 'bg-red-100 text-red-800'
}

const statusLabels = {
  success: 'Berhasil',
  processing: 'Diproses',
  failed: 'Gagal'
}

export default function HistoryPage() {
  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState('all')
  const [gameFilter, setGameFilter] = useState('all')

  const filteredTransactions = mockTransactions.filter(transaction => {
    const matchesSearch = transaction.id.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         transaction.game.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStatus = statusFilter === 'all' || transaction.status === statusFilter
    const matchesGame = gameFilter === 'all' || transaction.game === gameFilter
    
    return matchesSearch && matchesStatus && matchesGame
  })

  const uniqueGames = [...new Set(mockTransactions.map(t => t.game))]

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-white shadow-sm">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <Link href="/" className="flex items-center space-x-4">
              <h1 className="text-2xl font-bold text-primary">TopUp Store</h1>
            </Link>
            <nav className="hidden md:flex items-center space-x-6">
              <Link href="/" className="text-foreground hover:text-primary">Home</Link>
              <Link href="/games" className="text-foreground hover:text-primary">Games</Link>
              <Link href="/history" className="text-primary font-semibold">Riwayat</Link>
              <Link href="/help" className="text-foreground hover:text-primary">Bantuan</Link>
            </nav>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {/* Page Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">Riwayat Transaksi</h1>
          <p className="text-muted-foreground">
            Lihat semua transaksi top up game yang pernah Anda lakukan
          </p>
        </div>

        {/* Filters */}
        <Card className="mb-6">
          <CardHeader>
            <CardTitle>Filter Transaksi</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <Input
                  placeholder="Cari ID transaksi atau game..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
              </div>
              <div>
                <Select value={statusFilter} onValueChange={setStatusFilter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Filter Status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Semua Status</SelectItem>
                    <SelectItem value="success">Berhasil</SelectItem>
                    <SelectItem value="processing">Diproses</SelectItem>
                    <SelectItem value="failed">Gagal</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Select value={gameFilter} onValueChange={setGameFilter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Filter Game" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Semua Game</SelectItem>
                    {uniqueGames.map((game) => (
                      <SelectItem key={game} value={game}>
                        {game}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Transaction List */}
        <div className="space-y-4">
          {filteredTransactions.length === 0 ? (
            <Card>
              <CardContent className="py-12 text-center">
                <div className="w-16 h-16 bg-muted rounded-full flex items-center justify-center mx-auto mb-4">
                  <span className="text-2xl">ðŸ“‹</span>
                </div>
                <h3 className="text-lg font-semibold mb-2">Tidak ada transaksi</h3>
                <p className="text-muted-foreground mb-4">
                  {searchQuery || statusFilter !== 'all' || gameFilter !== 'all'
                    ? 'Tidak ada transaksi yang sesuai dengan filter Anda'
                    : 'Anda belum memiliki riwayat transaksi'}
                </p>
                <Link href="/">
                  <Button>Mulai Top Up</Button>
                </Link>
              </CardContent>
            </Card>
          ) : (
            filteredTransactions.map((transaction) => (
              <Card key={transaction.id} className="hover:shadow-md transition-shadow">
                <CardContent className="p-6">
                  <div className="flex flex-col md:flex-row md:items-center justify-between space-y-4 md:space-y-0">
                    <div className="flex-1">
                      <div className="flex items-center space-x-3 mb-2">
                        <h3 className="font-semibold text-lg">{transaction.game}</h3>
                        <Badge className={statusColors[transaction.status as keyof typeof statusColors]}>
                          {statusLabels[transaction.status as keyof typeof statusLabels]}
                        </Badge>
                      </div>
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-2 text-sm text-muted-foreground">
                        <div>
                          <span className="font-medium">ID Transaksi:</span> {transaction.id}
                        </div>
                        <div>
                          <span className="font-medium">Tanggal:</span> {new Date(transaction.date).toLocaleDateString('id-ID', {
                            year: 'numeric',
                            month: 'short',
                            day: 'numeric',
                            hour: '2-digit',
                            minute: '2-digit'
                          })}
                        </div>
                        <div>
                          <span className="font-medium">Paket:</span> {transaction.package}
                        </div>
                        <div>
                          <span className="font-medium">User ID:</span> {transaction.userId}
                        </div>
                        <div>
                          <span className="font-medium">Server:</span> {transaction.server}
                        </div>
                        <div>
                          <span className="font-medium">Total:</span> 
                          <span className="font-semibold text-foreground ml-1">
                            Rp {transaction.amount.toLocaleString('id-ID')}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="flex flex-col space-y-2 md:ml-4">
                      {transaction.status === 'success' && (
                        <Button variant="outline" size="sm">
                          Lihat Detail
                        </Button>
                      )}
                      {transaction.status === 'failed' && (
                        <Button variant="outline" size="sm">
                          Coba Lagi
                        </Button>
                      )}
                      {transaction.status === 'processing' && (
                        <Button variant="outline" size="sm">
                          Cek Status
                        </Button>
                      )}
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))
          )}
        </div>

        {/* Summary Stats */}
        {filteredTransactions.length > 0 && (
          <Card className="mt-8">
            <CardHeader>
              <CardTitle>Ringkasan</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4 text-center">
                <div>
                  <div className="text-2xl font-bold text-primary">
                    {filteredTransactions.length}
                  </div>
                  <div className="text-sm text-muted-foreground">Total Transaksi</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-green-600">
                    {filteredTransactions.filter(t => t.status === 'success').length}
                  </div>
                  <div className="text-sm text-muted-foreground">Berhasil</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-yellow-600">
                    {filteredTransactions.filter(t => t.status === 'processing').length}
                  </div>
                  <div className="text-sm text-muted-foreground">Diproses</div>
                </div>
                <div>
                  <div className="text-2xl font-bold">
                    Rp {filteredTransactions
                      .filter(t => t.status === 'success')
                      .reduce((sum, t) => sum + t.amount, 0)
                      .toLocaleString('id-ID')}
                  </div>
                  <div className="text-sm text-muted-foreground">Total Berhasil</div>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Help Section */}
        <Card className="mt-8">
          <CardContent className="pt-6">
            <div className="text-center">
              <h4 className="font-semibold mb-2">Butuh Bantuan?</h4>
              <p className="text-sm text-muted-foreground mb-4">
                Jika ada masalah dengan transaksi Anda, hubungi customer service kami
              </p>
              <div className="flex flex-col sm:flex-row gap-2 justify-center">
                <Button variant="outline" size="sm">
                  WhatsApp: +62 812-3456-7890
                </Button>
                <Button variant="outline" size="sm">
                  Email: support@topupstore.com
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
